import React, { useState } from "react";
import "./contact.css";
import { NavLink } from "react-router-dom";
import map1 from "../../assets/map1.svg";
import map2 from "../../assets/map2.svg";
import map3 from "../../assets/map3.svg";
import { Helmet } from "react-helmet";
const Contact = () => {
  const [user, setUser] = useState({
    fullname: "",
    email: "",
    phone: "",
    location: "",
    message: "",
  });

  let value, name;
  const handelerInput = (e) => {
    console.log(e);
    name = e.target.name;
    value = e.target.value;
    setUser({ ...user, [name]: value });
  };

  const PostData = async (e) => {
    e.preventDefault();

    const { fullname, email, phone,  location,  message} = user;

    const res = await fetch("/register", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        fullname,
        email,
        phone,
        location,
        message,
      }),
    });

    const data = await res.json();

    // I need to change the data to res
    if (data.status === 422 || !data) {
      window.alert("please fill the all data");
      console.log("please fill the all data");
    } else {
      window.alert("Submitted successfully");
      console.log("Submitted successfully");
      setUser({  fullname: "",
      email: "",
      phone: "",
      location: "",
      message: ""});
    }
  };

  return (
    <>
      <Helmet>
        <title>Contact Us | i7</title>
        <meta
          name="description"
          content="Company name provides customers with a comprehensive selection of waste management services focusing on green electronics recycling, waste disposal, hard drive data destruction and shredder and resource management strategies."
        />
      </Helmet>
      <section className="contact_top_section">
        <div className="container-fluid">
          <div className="row">
            <div className="col-lg-10 col-md-10 col-sm-10 col-12 mx-auto">
              <div className="about_top_banner">
                <h1>Contact Us</h1>
                <NavLink to="/about" className="contact_btn">
                  About Us
                </NavLink>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="map">
        <iframe
          width="600"
          height="450"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d62205.76671891818!2d77.6158920862314!3d12.980778061040395!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xe3e4281694d06515!2sRpl%20Techno%20It%20Services%20Pvt%20Ltd!5e0!3m2!1sen!2sin!4v1612898464918!5m2!1sen!2sin"
          frameborder="0"
          style={{ border: 0 }}
          allowfullscreen=""
          aria-hidden="false"
          tabindex="0"
        ></iframe>
      </section>
      <div className="container-fluid">
        <div className="row">
          <div className="col-lg-10 col-md-10 col-sm-10 col-12 mx-auto">
            <div className="row map_position">
              <div className="col-lg-4 col-md-4 col-12 map_fiexed">
                <div className="map_boxes">
                  <img className="map_images" src={map1} alt="" />
                  <h3>Our Phone</h3>
                  <p>+91 (0) 73373 45151</p>
                  {/* <p>(+91) 8874589566</p> */}
                </div>
              </div>
              <div className="col-lg-4 col-md-4 col-12 map_fiexed">
                <div className="map_boxes">
                  <img className="map_images" src={map2} alt="" />
                  <h3>Our Mail Box</h3>
                  <p>info@i7ewasterecyclers.com</p>
                  {/* <p>info@i7ewasterecyclers</p> */}
                </div>
              </div>
              <div className="col-lg-4 col-md-4 col-12 map_fiexed">
                <div className="map_boxes">
                  <img className="map_images" src={map3} alt="" />
                  <h3>Our Location</h3>
                  <p>
                    S/NO.9/4 Kacho Halli Industrial Estate, Laxmipura Post
                    Dhasanapura, Uttarahalli Hobli, Bengaluru, Karnataka 562123
                    India
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* contact form start here */}
      <section>
        <div className="container-fluid">
          <div className="row">
            <div className="col-lg-10 col-md-10 col-sm-10 col-12 mx-auto contact_form_heading">
              <h1>Latest Contact</h1>

              <div className="contact_form">
                <form method="POST">
                  <div class="mb-1 form-row">
                    <div class="form-group col-md-6">
                      <label htmlFor="fullname" class="form-label">
                        Full Name:
                      </label>
                      <input
                        type="text"
                        className="form-control form-group"
                        id="fullname"
                        name="fullname"
                        onChange={handelerInput}
                        value={user.fullname}
                        placeholder="Peter"
                      />
                    </div>

                    <div className="mb-1 form-group col-md-6">
                      <label htmlFor="email" class="form-label">
                        Email:
                      </label>
                      <input
                        type="email"
                        className="form-control form-group"
                        id="email"
                        name="email"
                        onChange={handelerInput}
                        value={user.email}
                        placeholder="name@example.com"
                      />
                    </div>
                  </div>
                  <div class="mb-1 form-row">
                    <div class="form-group col-md-6">
                      <label htmlFor="phone" class="form-label">
                        Phone:
                      </label>
                      <input
                        type="text"
                        className="form-control form-group"
                        id="phone"
                        name="phone"
                        onChange={handelerInput}
                        value={user.phone}
                        placeholder="88925 45786"
                      />
                    </div>

                    <div className="mb-1 form-group col-md-6">
                      <label htmlFor="location" class="form-label">
                        Location:
                      </label>
                      <input
                        type="text"
                        className="form-control form-group"
                        id="location"
                        name="location"
                        onChange={handelerInput}
                        value={user.location}
                        placeholder="e.g:Bangalore"
                      />
                    </div>
                  </div>
                  <div className="mb-1">
                    <label htmlFor="message" class="form-label">
                      Message:
                    </label>
                    <textarea
                      className="form-control form-group"
                      id="message"
                      rows="3"
                      name="message"
                      onChange={handelerInput}
                      value={user.message}
                      placeholder="message here..."
                    ></textarea>
                  </div>
                  <div class="mb-1 form-row">
                    <div class="form-group col-md-6 submit_my_btn">
                      <input
                        type="submit"
                        name="register"
                        className="btn btn_submit2"
                        value="Submit"
                        onClick={PostData}
                      />
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Contact;
